.card-link__btn {
  width: 119px;
  height: 32px;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  line-height: 24px;
  letter-spacing: -0.15px;
  background-color: #ff3e79;
  border-radius: 20px;
}
@media (min-width: 768px) {
  .card-link__btn {
    padding-left: 8px;
  }
}

.section__footer {
  display: flex;
  flex-direction: column;
}
.section__footer--text {
  margin: 0 16px 16px;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
}
@media (min-width: 768px) {
  .section__footer--text {
    width: 421px;
    margin: 0 24px 16px;
  }
}
.section__footer--link {
  width: 250px;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1b1c21;
  padding-left: 16px;
}
@media (min-width: 768px) {
  .section__footer--link {
    width: auto;
    padding-left: 24px;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  border-top: 1px solid #d9fff5;
}
@media (min-width: 768px) {
  .footer {
    padding-top: 32px;
    padding-left: 24px;
    padding-right: 24px;
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer--size {
  margin-top: 40px;
  padding-bottom: 19px;
  padding-top: 16px;
  font-size: 14px;
  color: #7e7e82;
  line-height: 16px;
}
.footer__info {
  text-align: center;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .footer__info {
    text-align: left;
    width: 240px;
  }
}
.footer__confi {
  text-align: center;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .footer__confi {
    width: 240px;
  }
}
.footer__text {
  text-align: center;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .footer__text {
    text-align: right;
    width: 240px;
  }
}

body {
  font-family: "TT Lakes", sans-serif;
  background-color: #ffffff;
}